import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ContactComponent } from '../views/contact/contact.component';
import { ContactContainerComponent } from '../views/contact-container/contact-container.component';
import { contactDataResolver } from '../resolvers/contact.resolver';

const routes: Routes = [
  {
    path: '',
    component: ContactContainerComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: ContactComponent,
        resolve: {
          contact: contactDataResolver
        }
      },
    ]
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class ContactRoutingModule { }
